nav{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    margin: 0 0 5% 0;
    position: fixed;
    z-index: 1;
}

.nav{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    text-align: center;
    width: 80%;
    background-color: rgb(7, 25, 82, 0.9);
    transition: all 300ms;
    border-radius: 0 0 10px 10px;
    
}

.nav li{
    list-style: none;
    z-index: 1;
    width: 300%;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.nav li a{
    font-size: 100%;
    padding: 20px;
    text-decoration: none;
    color: rgb(255, 255, 255);
    display: block;
    transition: all 300ms;
}

.nav li a:hover{
    color: rgb(0, 0, 0);
    border-radius: 0 50px 0 50px;
    padding: 10% 0%;
    background-color:rgb(151, 254, 237);
    cursor: pointer;
    transition: all 600ms;
    box-shadow: 10px 30px 100px 20px rgb(151, 254, 237);
}

@media(max-width: 900px){
    nav{
        display: none;
    }
}